<template>
	<div id="layout">
		<AsideComp></AsideComp>
    <HeaderComp :userInfo="userInfo"></HeaderComp>
    <keep-alive>
      <MainComp></MainComp>
    </keep-alive>
	</div>
</template>
<script>
import AsideComp from './Aside'
import HeaderComp from './Header'
import MainComp from './Main'
import { getMyInfo, getPermissionList } from '../api'
export default {
  name: 'layout',
  components: {
    AsideComp,
    HeaderComp,
    MainComp
  },
  data() {
    return {
      userInfo: null
    }
  },
  methods: {
    getMenu () {
      getPermissionList().then(res => {
        let permissionList = res.data.permissionList
        this.$store.dispatch('saveUserPermission', permissionList)
      })
    },
    // 获取当前用户信息
    getUserInfo () {
      getMyInfo().then(res => {
        if(res.data.code === '0001') {
          // this.userInfo = res.data.result.userInfo;
          this.$store.dispatch('saveUserInfo', res.data.result.userInfo)
        } else {
          this.$message.error(res.data.message)
        }
      })
      .catch(err => {
        console.log(err)
        this.$catchError(err)
      })
    }
  },
  mounted () {
    this.getUserInfo()
  }
}
</script>



