<template>
	<header class="header transition" :class="{'spread': sidebarStatus}">
		<div class="container-fluid">
			<div class="pull-left">
				<el-button type="primary" class="btn-toggle" @click="toggleSidebar"><i class="fa fa-bars"></i></el-button>
			</div>
			<div class="pull-right">
				<!-- <div class="provider-name">
					<span>欢迎您：北京鸿基国际旅行社有限责任公司第一分公司</span>
				</div> -->
				<el-dropdown trigger="click" @command="handleCommand">
				  <span class="el-dropdown-link">
				  	<img :src="userInfo ? userInfo.avatar : ''" class="avatar"/>
				    {{ userInfo ? userInfo.name : ''}}
				    <i class="el-icon-caret-bottom el-icon--right"></i>
				  </span>
				  <el-dropdown-menu slot="dropdown">
				    <el-dropdown-item command="home">
				    	<span class="link">回到首页</span>
				    </el-dropdown-item>
				    <el-dropdown-item command="ums">
				    	<span class="link">进入用户系统</span>
				    </el-dropdown-item>
				    <el-dropdown-item command="pay">
				    	<span class="link">进入支付系统</span>
				    </el-dropdown-item>
						<el-dropdown-item divided>
							<span class="link" @click="logout">退出</span>
						</el-dropdown-item>
				  </el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>
	</header>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { requestExit } from '../api'
export default {
	data () {
		return {
			home: '/admin/home',
		}
	},
  methods: {
  	...mapActions([
  		'toggleSidebar'
  	]),
  	handleCommand(command) {
  		if (command === 'home') {
  			this.$router.push('/home/welcome')
  		} else if (command === 'ums') {
  			window.open('https://transformgit.github.io/yueadmin-ums')
  		} else if (command === 'pay') {
  			window.open('https://transformgit.github.io/yueadmin-pay')
  		}
  	},
    logout() {
    	this.$confirm('确定退出系统?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
      	requestExit().then(res => {
      		console.log(res)
      		this.$router.push('/login')
      		this.$message('已退出登录')
      	})
      }).catch(err => {
      	console.log(err)
        this.$message('已取消操作')        
      })
    }
  },
  computed: {
	  ...mapGetters([
  		'sidebarStatus',
  		'userInfo'
  	])
  },
}
</script>
<style scoped lang="scss">
	.header {
		position: fixed;
		top: 0;
		left: 240px;
		right: 0;
		z-index: 999;
		background: #fff;
		box-shadow: 0 0 1px rgba(0,0,0,.25);
		&.spread {
			left: 0;
		}
	}
	.el-menu {
		border-radius: 0;
		background-color: rgba(255,255,255,.95);
	}
	.provider-name {
		float: left;
		height: 60px;
		padding: 0 15px;
		line-height: 60px;
		font-size: 16px;
	}
	.avatar {
		width: 20px;
		height: 20px;
		vertical-align: middle
	}
	.el-dropdown {
		width: 150px;
		line-height: 56px;
		overflow: hidden;
	  text-overflow: ellipsis;
	  white-space: nowrap;
	  .el-dropdown-link {
			padding: 10px;
			border-radius: 4px;
	    border: 1px solid #f0f0f8;
		}
	}
	.link {
		display: inline-block;
		width: 100%;
		color: #337ab7;
	}
	.el-dropdown-menu {
		width: 150px;
	}
	.btn-toggle {
		margin: 12px 5px;
	}
</style>



